/* register css */
.bg-light {
  background-color: #ffff !important;
}
body {
color:yellow;
background: #63738a;
font-family: 'Roboto', sans-serif;
height: 100%
}

#contact-form{
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  margin-top: 100px;
  margin-bottom: 100px;
  margin-left: 200px;
  margin-right: 200px;
  gap:20px;
}
.center_button{
  text-align: center;
}

.form-control {
height: 40px;
box-shadow: none;
color: #969fa4;
}
.form-control:focus {
border-color: #5cb85c;
}
.form-control, .btn {        
border-radius: 3px;
}
.signup-form {
width: 450px;
margin: 0 auto;
padding: 30px 0;
  font-size: 15px;
}
.signup-form h2 {
color: #636363;
margin: 0 0 15px;
position: relative;
text-align: center;
}
/* .signup-form h2:before, .signup-form h2:after {
content: "";
height: 2px;
width: 30%;
background: #d4d4d4;
position: absolute;
top: 50%;
z-index: 2;
}	 */
.signup-form h2:before {
left: 0;
}
.signup-form h2:after {
right: 0;
}
.signup-form .hint-text {
color: #999;
margin-bottom: 30px;
text-align: center;
}
.signup-form form {
color: #999;
border-radius: 3px;
margin-bottom: 15px;
background: #f2f3f7;
box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
padding: 30px;
}
.signup-form .form-group {
margin-bottom: 20px;
}
.signup-form input[type="checkbox"] {
margin-top: 3px;
}
.signup-form .btn {        
font-size: 16px;
font-weight: bold;		
min-width: 140px;
outline: none !important;
}
.signup{
  display: flex;
  flex-direction: column;
  text-align: center;
}
.signup-form .row div:first-child {
padding-right: 10px;
}
.signup-form .row div:last-child {
padding-left: 10px;
}    	
.signup-form a {
color: rgb(88, 86, 86);
text-decoration: underline;
}
.signup-form a:hover {
text-decoration: none;
}
.signup-form form a {
color: #5cb85c;
text-decoration: none;
}	
.signup-form form a:hover {
text-decoration: underline;
} 
.middle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.bgimg {
  /* Background image */
  background-color: red;
  
}
/* login css */
  
.login-form {
  width: 400px;
  margin: 30px auto;
}
.float-left form-check-label{
  display: flex;
  flex-direction: column;
  text-align: center;
}
.float-right{
  display: flex;
  flex-direction: column;
  text-align: center;
}
.login-form form {        
  margin-bottom: 15px;
  background: #f7f7f7;
  gap: 10px;  
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  padding: 30px;
}
.login-form h2 {
  margin: 0 0 15px;
}
.form-control, .login-btn {
  border-radius: 2px;
}
.form-control{
  width:500px;
}
.login-btn{
  text-align: center;
  width: 100px;
  margin-left: 120px;
  margin-right: 100px;
}
.input-group-prepend .fa {
  font-size: 18px;
}
.login-btn {
  font-size: 15px;
  font-weight: bold;
  min-height: 40px;
}
.social-btn .btn {
  border: none;
  margin: 10px 3px 0;
  opacity: 1;
}
.social-btn .btn:hover {
  opacity: 0.9;
}
.social-btn .btn-secondary, .social-btn .btn-secondary:active {
  background: #507cc0 !important;
}
.social-btn .btn-info, .social-btn .btn-info:active {
  background: #64ccf1 !important;
}
.social-btn .btn-danger, .social-btn .btn-danger:active {
  background: #df4930 !important;
}
.or-seperator {
  margin-top: 20px;
  text-align: center;
  border-top: 1px solid #ccc;
}
.or-seperator i {
  padding: 0 10px;
  background: #f7f7f7;
  position: relative;
  top: -11px;
  z-index: 1;
}   
/* About css */


h2 {
color: #333;
text-align: center;
text-transform: uppercase;
font-family: "Roboto", sans-serif;
font-weight: bold;
position: relative;
margin-top: 1em!important;
}
.h2{
margin-top: 1em!important;
}
h2::after {
content: "";
width: 100px;
position: absolute;
margin: 0 auto;
height: 3px;
background: #ffdc12;
left: 0;
right: 0;
bottom: -10px;
}
.carousel {
width: 650px;
margin: 0 auto;
padding-bottom: 50px;
}
.carousel .carousel-item {
color: #999;
font-size: 14px;
text-align: center;
overflow: hidden;
min-height: 340px;
margin-top: 2em;
}
.carousel .carousel-item a {
color: #eb7245;
}
.carousel .img-box {
width: 145px;
height: 145px;
margin: 0 auto;
border-radius: 50%;
}
.carousel .img-box img {
width: 100%;
height: 100%;
display: block;
border-radius: 50%;
}
.carousel .testimonial {	
padding: 30px 0 10px;
}
.carousel .overview {	
text-align: center;
padding-bottom: 5px;
}
.carousel .overview b {
color: #333;
font-size: 15px;
text-transform: uppercase;
display: block;	
padding-bottom: 5px;
}
.carousel .star-rating i {
font-size: 18px;
color: #ffdc12;
}
.carousel-control-prev, .carousel-control-next {
width: 30px!important;
height: 30px!important;
border-radius: 50%!important;
background: #999!important;
text-shadow: none!important;
top: 4px!important;
}
.carousel-control-prev i, .carousel-control-next i {
font-size: 20px!important;
margin-right: 2px!important;
}
.carousel-control-prev {
left: auto!important;
right: 40px!important;
}
.carousel-control-next i {
margin-right: -2px!important;
}
.carousel .carousel-indicators {
bottom: 15px!important;
}
.carousel-indicators li, .carousel-indicators li.active {
width: 11px!important;
height: 1px!important;
margin: 1px 5px!important;
border-radius: 50%!important;
}
.carousel-indicators li {	
background: #e2e2e2!important;
border: none!important;
}
.carousel-indicators li.active {		
background: #888!important;		
}

#notfound {
  position: absolute;
  height:100vh;
}
#notfound .notfound {
  position: absolute;
  left: 50%;
  top:50%;
  -webkit-transform:translate(-50%,-50%);
  -ms-transform:translate(-50%,-50%);
  transform:translate(-50%,-50%);
}
.notfound{
  max-width:920px;
  width: 100%;
  line-height:1.4;
  text-align: center;
  padding-left: 15px;
  padding-right: 15px;
}

.notfound .notfound-404 h1{
  font-family: "Maven Pro",sans-serif;
  color: #ececec;
  font-weight: 900;
  font-size: 276px;
  margin:0px;
  position: absolute;
  left:50%;
  top:50%;
  -webkit-transform:translate(-50%,-50%);
  -ms-transform:translate(-50%,-50%);
  transform:translate(-50%,-50%);
}
.notfound h2{
  font-family: "Maven Pro",sans-serif;
  font-size: 46px;
  color: #000;
  font-weight: 900;
  text-transform: uppercase;
  margin: 0px;
}
.notfound p{
  font-family: "Maven Pro",sans-serif;
  font-size: 16px;
  color: #000;
  font-weight: 400;
  text-transform: uppercase;
  margin-top: 15px;
}
.notfound a{
  font-family: "Maven Pro",sans-serif;
  font-size: 14px;
  text-decoration: none;
  background: #189cf0;
  display: inline-block;
  padding: 16px 38px;
  border: 2px solid transparent;
  border-radius: 40px;
  color: #fff;
  font-weight: 400;
  -webkit-transition: 0.2s all;
  transition: 0.2s all;
  text-transform: uppercase;
}
.notfound a:hover{
  background-color: #fff;
  border-color: #189cf0;
  color: #189cf0;
}
@media only screen and(max-width:480px) {
  .notfound .notfound-404-h1 {
    font-size: 162px;
  }
  .notfound h2{
    font-size: 26px;
  }
}

